import keyboardPackage from '../../components/keyboard-package/keyboard-package.vue';
import passwordInput from '../../components/password-input/password-input.vue';
export default {
    data() {
        return {
            n: true,
            name: '',
            cool: '',
            numberList: [],
            length: 6,
            passIn: false,
            days: '',
            shadrate: '',
            id: '',
            password: ''
        };
    },
    components: {
        keyboardPackage,
        passwordInput
    },
    onShow(option) {
        this.name = option.name;
        this.cool = option.cllo;
        this.id = option.ids;
        this.days = option.day;
        this.shadrate = option.shadrate;
        console.log(this.name);
        console.log(this.cool);
        console.log(this.id);
        console.log(this.days);
        console.log(this.shadrate);
    },
    methods: {
        clo: function() {
            this.passIn = false;
            this.$refs['number'].close();
            this.numberList.length = 0;
        },
        onChange(e) {
            if (e.show == false) {
                this.passIn = false;
            }
            this.numberList.length = 0;
        },
        onDelete() {
            this.numberList.pop();
        },
        onConfirm() {
            if (this.numberList.length != 6) {
                uni.showToast({
                    title: '请输入六位交易密码！',
                    duration: 2000,
                    icon: 'none'
                });
            }
        },
        onInput(val) {
            var that = this;
            that.numberList.push(val);
            that.password = that.numberList.join().replace(/,/g, '');
            if (that.numberList.length >= that.length) {
                uni.request({
                    url: that.url + 'cloudtransfer/',
                    method: 'POST',
                    data: {
                        name: this.name,
                        cloudid: this.id,
                        mobile: this.cool,
                        password: this.password
                    },
                    header: {
                        Authorization: 'JWT' + ' ' + that.global_.token
                    },
                    success(res) {
                        if (res.statusCode == 400) {
                            uni.showToast({
                                icon: 'none',
                                title: '未实名认证通过'
                            });
                        }
                        if (res.statusCode == 401) {
                            that.numberList.pop();
                            that.numberList.length = 0;
                            that.$refs.wrong.flag = false;
                            var n = res.data.data.err_num;
                            var s = 5 - n;
                            that.$refs.wrong.tip = '剩余' + s + '次机会';
                        }
                        if (res.statusCode == 200) {
                            that.passIn = false;
                            that.$refs['number'].close();
                            uni.navigateTo({
                                url: '../successful/successful'
                            });
                            uni.showToast({
                                title: '付款完成',
                                duration: 3000
                            });
                        }
                        if (res.statusCode == 303) {
                            uni.showToast({
                                icon: 'none',
                                title: '该用户不存在'
                            });
                        }
                        var page = getCurrentPages().pop();
                        if (page == undefined || page == null) return;
                    }
                });
                this.numberList.length = 0;
            }
        },
        btn: function() {
            var that = this;
            this.passIn = true;
            this.$refs['number'].open();
            this.onInput(val);
        }
    }
};
